<script>
import barCharts from './barCharts'
export default {
    name:'zyts',
    data(){
        return{
            list: [],
            pagesize: 10,
            currpage: 1,
            activeName: 'first',
            xData:[],
            yData:[],
            chart:false,
            baroption1:{
                id:'chart1',
                title: '国家示范专业数量同比图',
                param:[],
                dm:[],
                btn:'1',
                xAxis: [
                    {
                        data: ['信息工程系', '机电工程汽车系', '人文系', '财贸经济系', '外语系', '规划建筑工程系', '园林与食品工程学院', '互联网学院', '艺术系'],
                        axisLabel: {
                            rotate:0,
                        }
                    }
                ],
                yAxis: [
                    {
                        name:'(人)',
                    }
                ],
                series: [{
                    name: '人数',
                    data: [1570, 1300, 1146, 915, 710, 616, 500, 415, 320],
                }]
            },
            baroption2:{
                id:'chart2',
                title: '省重点专业数量同比图',
                param:[],
                dm:[],
                btn:'1',
                xAxis: [
                    {
                        data: ['信息工程系', '机电工程汽车系', '人文系', '财贸经济系', '外语系', '规划建筑工程系', '园林与食品工程学院', '互联网学院', '艺术系'],
                        axisLabel: {
                            rotate:0,
                        }
                    }
                ],
                yAxis: [
                    {
                        name:'(人)',
                    }
                ],
                series: [{
                    name: '人数',
                    data: [1570, 1300, 1146, 915, 710, 616, 500, 415, 320],
                }]
            },
            baroption3:{
                id:'chart3',
                title: '市品牌专业数量同比图',
                param:[],
                dm:[],
                btn:'1',
                xAxis: [
                    {
                        data: ['信息工程系', '机电工程汽车系', '人文系', '财贸经济系', '外语系', '规划建筑工程系', '园林与食品工程学院', '互联网学院', '艺术系'],
                        axisLabel: {
                            rotate:0,
                        }
                    }
                ],
                yAxis: [
                    {
                        name:'(人)',
                    }
                ],
                series: [{
                    name: '人数',
                    data: [1570, 1300, 1146, 915, 710, 616, 500, 415, 320],
                }]
            },
            baroption4:{
                id:'chart4',
                title: '其它特色专业数量同比图',
                param:[],
                dm:[],
                btn:'1',
                xAxis: [
                    {
                        data: ['信息工程系', '机电工程汽车系', '人文系', '财贸经济系', '外语系', '规划建筑工程系', '园林与食品工程学院', '互联网学院', '艺术系'],
                        axisLabel: {
                            rotate:0,
                        }
                    }
                ],
                yAxis: [
                    {
                        name:'(人)',
                    }
                ],
                series: [{
                    name: '人数',
                    data: [1570, 1300, 1146, 915, 710, 616, 500, 415, 320],
                }]
            },
            tableData: [
                {
                    date: '2016-05-02',
                    name: '王小虎',
                    address: '上海市普陀区金沙江路 1518 弄'
                }, {
                    date: '2016-05-04',
                    name: '张小虎',
                    address: '上海市普陀区金沙江路 1517 弄'
                }, {
                    date: '2016-05-01',
                    name: '李小虎',
                    address: '上海市普陀区金沙江路 1519 弄'
                }, {
                    date: '2016-05-03',
                    name: '赵小虎',
                    address: '上海市普陀区金沙江路 1516 弄'
                }
            ],
        }
    },
    methods: {
        handleClick(tab, event) {
            // console.log(tab, event);
        },
        formatter(row, column) {
            return row.address;
        },
        handleCurrentChange(cpage) {
            this.currpage = cpage;
        },
        handleSizeChange(psize) {
            this.pagesize = psize;
        }
    },
    components: {
        barCharts,
    },
    mounted() {
        var _this = this
        $.ajax({
            type: "post",
            url: _this.baseUrl+"/ydjxzg/hx/xsgl.action",
            data: {reg:'xxzyts',style:'1'},
            dataType: "json",
            success: function (res) {
                if (res.code == 401) {
                    location.href = _this.baseUrl01 
                    console.log('判断进入了401，马上开始跳转');  
                }else if (res.code == 403) {
                    console.log('判断进入了403，马上开始跳转');
                    sessionStorage.setItem("login","nop");
                    // location.href = "http://zg.demo.xqit.net/#/p403"
                    
                    _this.$router.push({
                        path: "/p403m",
                        name: "p403m"
                    })
                }else{
                    console.log(res)
                    if(res.gjsfzysltbt){
                        _this.xData = []
                        _this.yData = []
                        _this.baroption1.param.push(res.gjsfzysltbt[0].param1)
                        for(var i = 1;i < res.gjsfzysltbt.length;i++){
                            _this.xData.push(res.gjsfzysltbt[i].mc)
                            _this.yData.push(res.gjsfzysltbt[i].zcount)
                            if(res.gjsfzysltbt[i].dm){
                                _this.baroption1.dm.push(res.gjsfzysltbt[i].dm)
                            }
                        }
                        // this.baroption1.yAxis[0].name = '㎡'
                        _this.baroption1.xAxis[0].data = _this.xData;
                        _this.baroption1.series[0].data = _this.yData;
                    }
                    if(res.szdzysltbt){
                        _this.xData = []
                        _this.yData = []
                        _this.baroption2.param.push(res.szdzysltbt[0].param1)
                        for(var i = 1;i < res.szdzysltbt.length;i++){
                            _this.xData.push(res.szdzysltbt[i].mc)
                            _this.yData.push(res.szdzysltbt[i].zcount)
                            if(res.szdzysltbt[i].dm){
                                _this.baroption2.dm.push(res.szdzysltbt[i].dm)
                            }
                        }
                        // this.baroption2.yAxis[0].name = '㎡'
                        _this.baroption2.xAxis[0].data = _this.xData;
                        _this.baroption2.series[0].data = _this.yData;
                    }
                    if(res.sppzysltbt){
                        _this.xData = []
                        _this.yData = []
                        _this.baroption3.param.push(res.sppzysltbt[0].param1)
                        for(var i = 1;i < res.sppzysltbt.length;i++){
                            _this.xData.push(res.sppzysltbt[i].mc)
                            _this.yData.push(res.sppzysltbt[i].zcount)
                            if(res.sppzysltbt[i].dm){
                                _this.baroption3.dm.push(res.sppzysltbt[i].dm)
                            }
                        }
                        // this.baroption3.yAxis[0].name = '㎡'
                        _this.baroption3.xAxis[0].data = _this.xData;
                        _this.baroption3.series[0].data = _this.yData;
                    }
                    if(res.qttszytbt){
                        _this.xData = []
                        _this.yData = []
                        _this.baroption4.param.push(res.qttszytbt[0].param1)
                        for(var i = 1;i < res.qttszytbt.length;i++){
                            _this.xData.push(res.qttszytbt[i].mc)
                            _this.yData.push(res.qttszytbt[i].zcount)
                            if(res.qttszytbt[i].dm){
                                _this.baroption4.dm.push(res.qttszytbt[i].dm)
                            }
                        }
                        // this.baroption4.yAxis[0].name = '㎡'
                        _this.baroption4.xAxis[0].data = _this.xData;
                        _this.baroption4.series[0].data = _this.yData;
                    }
                    _this.chart = true
                }
            },
            error:function(err){
                console.log(err)
            }
        });
    },
}
</script>

<style>
#zhuanye{
    width: 100%;
    height: 100%;
}
#zhuanye .row{
    display:flex;
    justify-content:center;
}
#zhuanye .title{
    margin: 20px 0;
    text-align: center;
    font-size: 30px;
    font-weight: 900;
    letter-spacing: 10px;
}
#zhuanye .row .col{
    width:750px;
    margin-top:20px;
}
#zhuanye .row .cardtitle{
    font-size:25px;
    text-align:center;
    font-weight:900;
    margin-top:-20px;
    margin-bottom:-20px;
}
#zhuanye .row .coltips{
    min-width:280px;
    max-width:300px;
    margin-top:20px;
}
#zhuanye .row .coltips2{
    min-width:150px;
    max-width:300px;
    margin-top:20px;
}
/* #zhuanye .shaixuan_box{
    position:relative;
    left:13%;
    bottom:32px;
} */
#zhuanye .row .tipheight{
    height:435px;
}
#zhuanye .row .tip_select{
    height:465px;
}
#zhuanye .row .tips{
    font-size:25px;
    text-align:left;
    font-weight:900;
    margin-top:-20px;
    margin-bottom:-20px;
    padding:0 20px;
}
#zhuanye .row .tippoint{
    margin-top:50px;
    padding:0 20px;
    line-height:40px;
}
#zhuanye .is-active{
    color: #029348 !important;
}
.el-tabs__active-bar{
    background: #029348 !important;
}
#zhuanye .is-top :hover{
    color: #029348 !important;
}
#zhuanye .is-left :hover{
    color: #029348 !important;
}
#zhuanye .el-card{
    padding: 40px 0 40px 0;
}
#zhuanye .shaixaun{
    padding: 82px 0 32px 0;
}
.el-tabs__content{
    overflow:inherit;
    padding-bottom: 20px;
}
.el-card{
    transition: 0s;
}
</style>




